.faculty-login{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    color: white;
}
.faculty-login-form{
    display: flex;
    flex-direction: column;
    
    margin-top: 20vh;
    
    
}
.email{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly !important;
}
.faculty-email,.faculty-password{
    border: none;
    width: 220px;
    height: 50px;
}
.password{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly !important;
    margin-top: 40px;
}
.faculty-submit-button{
    margin-top: 35px;
    width: 150px;
    height: 30px;
    cursor: pointer;
    border: none;
    border-radius: 20px;
    align-self: center;
    margin-left: 70px;
}
.faculty-submit-button:hover{
    margin-top: 35px;
    cursor: pointer;
    border: none;
    border-radius: 20px;
    background-color: greenyellow;
    color: red;
    box-shadow: 15px 10px 70px #5269a1;
    font-weight: 900;
    align-self: center;
}
