.navbar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100vw;
    height: auto;
    background-color: #192a56;
    padding: 15px;
    color: white;
}
.content{
    display: flex;
    flex-direction: row;
    width: 100vw;
    height: auto;
    justify-content: space-between;
    background-color: white;
}

@media only screen and (max-width:1050px)
{
    .left-content{
        display: none;
    }
    .right-content{
        
        width: 60vw;
    }
    .middle-content{
        width: 40vw;
    }
    
}
@media only screen and (min-width:1050px)
{
    .left-content{
        width: 25vw;
    }
    .right-content{
        
        width: 50vw;
    }
    
}
@media only screen and (max-width:600px)
{
    .left-content{
        display: none;
    }
    .right-content{
        
        display: none;
    }
    .middle-content{
        width: 100vw;
        
    }
    
}


.content button{
    border: none;
    padding: 15px;
    font-size: 15px;
    border-radius: 15px;
}

.left-content{
    background-color:#192a56;
    padding: 15px;
    height: 90vh;
}

.right-content{
    background-color: rgb(255, 255, 255);
    padding: 15px;
   height: 90vh;
   
}
.middle-content{
    background-color: rgb(255, 255, 255);
    padding: 15px;
   height: 90vh;
   
}
.middle-content-heading{
    display: flex;
}
.middle-content-heading button{
    margin-left: 20px;
}

.assignment-details{
    padding: 10px;
}

.faculty-details{
    padding: 10px;
    color: white;
    
}
.left-content-heading{
    display: flex;
}
.left-content-heading button{
    margin-left: 20px;
}
.right-content-heading{
    display:flex;
}
.right-content-heading button{
    margin-left: 20px;
}
.task-details{
    padding: 10px;
    background-color: rgb(235, 235, 235);
    margin-top: 15px;
    overflow: scroll;
    height: 80vh;
}
.assignment-details .submit-button{
    border: none;
    padding: 5px 25px;
    background-color: rgb(125, 206, 125);
    cursor: pointer;
}