.navbar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100vw;
    height: auto;
    background-color: #192a56;
    padding: 15px;
    color: white;
}
.content{
    display: flex;
    flex-direction: row;
    width: 100vw;
    height: auto;
    justify-content: space-between;
    background-color: white;
}

@media only screen and (max-width:1050px)
{
    .s-left-content{
        display: none;
    }
    .s-right-content{
        display: block;
        width: 100vw;
    }
    
}
@media only screen and (min-width:1050px)
{
    .s-left-content{
        width: 25vw;
    }
    .s-right-content{
        
        width: 75vw;
    }
    
}


.content button{
    border: none;
    padding: 15px;
    font-size: 15px;
    border-radius: 15px;
}

.s-left-content{
    background-color:#192a56;
    padding: 15px;
    height: 90vh;
}

.s-right-content{
    background-color: rgb(255, 255, 255);
    padding: 15px;
   height: 90vh;
   
}

.student-details{
    padding: 10px;
    color: white;
    
}
.s-left-content-heading{
    display: flex;
}
.s-left-content-heading button{
    margin-left: 20px;
}
.s-right-content-heading{
    display:flex;
}
.s-right-content-heading button{
    margin-left: 20px;
}
.task-details{
    padding: 10px;
    background-color: rgb(235, 235, 235);
    margin-top: 15px;
    overflow: scroll;
    height: 80vh;
}