.homeboard{
  
    display: flex;
    flex-direction: column;
    
    height: 100%;
    width: 100%;
}
.homeboard-button-student{
    width: 200px;
    height: 50px;
    border: none;
    cursor: pointer;
    margin-top: 15vh;
    border-radius: 10px;
}
.homeboard-button-faculty{
    width: 200px;
    height: 50px;
    border: none;
    cursor: pointer;
    margin-top: 5vh;
    border-radius: 10px;
}

.homeboard-button-student:hover,.homeboard-button-faculty:hover{
    width: 220px;
    background-color: black;
    color: white;
    box-shadow: 15px 10px 70px #5269a1;
}

.homeboard-welcome{
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 60px;
    height: 20vh;
    
}

.homeboard-content{
    display: flex;
    flex-direction: column;
    
    align-items: center;
    height: 80vh;
}
.homeboard-header{
    background-color: #192a56;
}
